export default {
	props: {
		title: {
			type: String,
			default: '标题'
		},
		time: {
			type: String,
			default: '00:00'
		},
		height: {
			type: Number,
			default: 240
		},
		rangeArr: {
			type: Array,
			default: () => []
		},
		mode: {
			type: String,
			default: 'selector'
		},
		value: {
			type: String,
			default: '00:00'
		}
	},
	data() {
		return {
			isHover: false
		}
	},
	methods: {
		// 控制卡片点击特效
		handleCardTouchStart() {
			if(this.isHover) {
				return
			}
			this.isHover = true
			setTimeout(() => {
				this.isHover = false
			}, 1000)
		},
		handleChange(e) {
			console.log(e)
			this.$emit('change', e.target.value)
		}
	}
}
